import Box from "@mui/material/Box";
import { styled } from "@mui/system";
import { useSharedState } from "../context";
import { Link } from "react-router-dom";

interface ExtendedDocument extends HTMLBodyElement {
  webkitRequestFullScreen: () => void;
}

const OuterBox = styled(Box)({
  width: "100%",
  height: "100vh",
  display: "flex",
  background: "black",
  justifyContent: "center",
  alignItems: "center",
});

const InnerBox = styled(Link)({
  color: "white",
  fontWeight: "bold",
  fontSize: 30,
  textDecoration: "none",
});

export const StartScreen = () => {
  const [_, setState] = useSharedState();
  const handleClick = () => {
    const doc = document.body as ExtendedDocument;
    doc.webkitRequestFullScreen();
    setState((prev) => ({
      ...prev,
      fullScreen: true,
    }));
  };

  return (
    <OuterBox>
      <InnerBox to="/game" onClick={handleClick}>
        Enable fullscreen
      </InnerBox>
    </OuterBox>
  );
};
