import Box from "@mui/material/Box";
import { Canvas } from "../Canvas";
import { ControlPanel } from "../ControlPanel";
import { useKeyDown, useKeyUp } from "../../../hooks";
import { useDispatch } from "react-redux";
import { setDir } from "../../../app/slices/game_slice";

export const Desktop = () => {
  const dispatch = useDispatch();
  const handleUP = () => dispatch(setDir("UP"));
  const handleDOWN = () => dispatch(setDir("DOWN"));
  const handleLEFT = () => dispatch(setDir("LEFT"));
  const handleRIGHT = () => dispatch(setDir("RIGHT"));
  const handleKeyUp = () => dispatch(setDir(null));

  // useKeyUp("_", handleKeyUp);
  useKeyDown("KeyW", handleUP);
  useKeyDown("KeyS", handleDOWN);
  useKeyDown("KeyA", handleLEFT);
  useKeyDown("KeyD", handleRIGHT);

  return (
    <Box height="100vh" width="100%" display="flex">
      <Box
        height="100%"
        width="50%"
        display="flex"
        alignItems="center"
        justifyContent={"center"}
      >
        <Canvas />
      </Box>
      <Box width="50%">
        <ControlPanel />
      </Box>
    </Box>
  );
};
