import React, { useState } from "react";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import { useDispatch, useSelector } from "react-redux";
import { setSize, reset } from "../../../app/slices/game_slice";
import { RootState } from "../../../app/store";

export const ControlPanel = () => {
  const dispatch = useDispatch();
  const width = useSelector((state: RootState) => state.game.width);
  const height = useSelector((state: RootState) => state.game.height);
  const obstacles = useSelector((state: RootState) => state.game.obstacles);
  const dir = useSelector((state: RootState) => state.game.dir);

  const [w, setW] = useState(width.toString());
  const [h, setH] = useState(height.toString());
  const [o, setO] = useState(obstacles.toString());

  const handleClick = () => {
    dispatch(setSize({ w: +w, h: +h, o: +o }));
  };

  const handleReset = () => {
    dispatch(reset());
  };

  return (
    <Box height="100vh" width="100%">
      <Box display="flex" alignItems={"center"} padding={1}>
        <TextField
          label="width"
          style={{ margin: "10px" }}
          value={w}
          onChange={(e) => setW(e.target.value)}
        />
        <TextField
          label="height"
          style={{ margin: "10px" }}
          value={h}
          onChange={(e) => setH(e.target.value)}
        />
        <TextField
          label="obstacles"
          style={{ margin: "10px" }}
          value={o}
          onChange={(e) => setO(e.target.value)}
        />
      </Box>
      <Box display="flex" alignItems={"center"} margin={2}>
        <Button variant="outlined" onClick={handleClick}>
          Generate
        </Button>
        <Button variant="outlined" onClick={handleReset}>
          reset
        </Button>
      </Box>
      <Box display="flex" alignItems={"center"} margin={3}>
        Direction:{" "}
        <Button style={{ marginLeft: 10 }} variant="outlined">
          {dir ?? "NULL"}
        </Button>
      </Box>
    </Box>
  );
};
