import React, { createContext, useState, useContext } from "react";

const initialState = {
  fullScreen: false,
  direction: "NULL",
  startX: 0,
  startY: 0,
  currentX: 0,
  currentY: 0,
  draw_controls: false,
};

const useMyState = () => useState(initialState);

type ContextType = ReturnType<typeof useMyState> | null;

const MyContext = createContext<ContextType>(null);

export const useSharedState = () => {
  const value = useContext(MyContext);
  if (value === null) throw new Error("Please add SharedStateProvider");
  return value;
};

export const SharedStateProvider: React.FC = ({ children }) => (
  <MyContext.Provider value={useMyState()}>{children}</MyContext.Provider>
);
