import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface GameState {
  width: number;
  height: number;
  obstacles: number;
  dir: "UP" | "DOWN" | "LEFT" | "RIGHT" | null;
  counter: number;
  world: any[];
  playerX: number;
  playerY: number;
  obs_pos: any[];
}

function getRandomInt(min: number, max: number) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min)) + min; //  ,  
}

const getInitialState = (
  width: number = 20,
  height: number = 20,
  obstacles: number = 0
) => {
  const state: GameState = {
    width,
    height,
    obstacles,
    dir: null,
    counter: 0,
    world: [],
    playerY: 1,
    playerX: 1,
    obs_pos: [],
  };

  for (let i = 0; i < obstacles; i++) {
    const pos = {
      x: Math.floor(Math.random() * width),
      y: Math.floor(Math.random() * height),
    };
    state.obs_pos.push(pos);
  }

  for (let y = 0; y < height; y++) {
    for (let x = 0; x < width; x++) {
      if (x === 1 && y === 1) state.world.push({ x, y, char: "A" });
      else if (state.obs_pos.some((item) => item.x === x && item.y === y))
        state.world.push({ x, y, char: "+" });
      else state.world.push({ x, y, char: "." });
    }
  }

  return state;
};

const initialState = getInitialState();

export const gameSlice = createSlice({
  name: "game",
  initialState,
  reducers: {
    setSize: (
      state,
      action: PayloadAction<{ w: number; h: number; o: number }>
    ) => {
      state.width = action.payload.w;
      state.height = action.payload.h;
      state.obstacles = action.payload.o;
      Object.assign(
        state,
        getInitialState(action.payload.w, action.payload.h, action.payload.o)
      );
    },

    setDir: (state, action: PayloadAction<GameState["dir"]>) => {
      state.dir = action.payload;
    },
    tick: (state) => {
      state.counter += 1;

      if (state.dir !== null) {
        state.world = state.world.filter((item) => {
          const flag1 = item.x === state.playerX;
          const flag2 = item.y === state.playerY;
          return !(flag1 && flag2);
        });
        switch (state.dir) {
          case "UP":
            if (
              state.obs_pos.some(
                (item) =>
                  item.x === state.playerX && item.y === state.playerY - 1
              )
            )
              state.dir = null;
            else if (state.playerY > 0) state.playerY -= 1;
            else state.dir = null;
            break;
          case "DOWN":
            if (
              state.obs_pos.some(
                (item) =>
                  item.x === state.playerX && item.y === state.playerY + 1
              )
            )
              state.dir = null;
            else if (state.playerY < state.height - 1) state.playerY += 1;
            else state.dir = null;
            break;
          case "LEFT":
            if (
              state.obs_pos.some(
                (item) =>
                  item.x === state.playerX - 1 && item.y === state.playerY
              )
            )
              state.dir = null;
            else if (state.playerX > 0) state.playerX -= 1;
            else state.dir = null;
            break;
          case "RIGHT":
            if (
              state.obs_pos.some(
                (item) =>
                  item.x === state.playerX + 1 && item.y === state.playerY
              )
            )
              state.dir = null;
            else if (state.playerX < state.width - 1) state.playerX += 1;
            else state.dir = null;
            break;
        }

        state.world = state.world.concat({
          x: state.playerX,
          y: state.playerY,
          char: "A",
        });
      }
    },
    reset: (state) => {
      Object.assign(state, getInitialState(state.width, state.height));
    },
  },
});

// Action creators are generated for each case reducer function
export const { setSize, setDir, tick, reset } = gameSlice.actions;

export default gameSlice.reducer;
