import React, { useRef, useEffect, useCallback, useState } from "react";
import { useSharedState } from "../context";

export const Canvas = (
  props: JSX.IntrinsicAttributes &
    React.ClassAttributes<HTMLCanvasElement> &
    React.CanvasHTMLAttributes<HTMLCanvasElement>
) => {
  const canvaRef = useRef<HTMLCanvasElement>(null);
  const [state] = useSharedState();

  const draw = useCallback(
    (ctx: CanvasRenderingContext2D, frameCount: number) => {
      ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

      if (state.draw_controls) {
        ctx.beginPath();
        ctx.arc(state.startX, state.startY, 40, 0, 2 * Math.PI);
        ctx.stroke();

        ctx.font = "10";
        ctx.textAlign = "center";
        ctx.textBaseline = "middle";
        ctx.fillText(state.direction, state.startX, state.startY);

        ctx.beginPath();
        ctx.arc(state.currentX, state.currentY, 20, 0, 2 * Math.PI);
        ctx.stroke();
      }
    },
    [state.direction, state.draw_controls, state.currentX, state.currentY]
  );

  const canvas_obj = (ele: HTMLCanvasElement) => {
    let returnable: any = {
      canvas: ele,
      ctx: ele.getContext("2d"),
      dpi: window.devicePixelRatio,
    };
    returnable.get = {
      style: {
        height() {
          return +getComputedStyle(ele).getPropertyValue("height").slice(0, -2);
        },
        width() {
          return +getComputedStyle(ele).getPropertyValue("width").slice(0, -2);
        },
      },
      attr: {
        height() {
          return returnable.ele.getAttribute("height");
        },
        width() {
          return returnable.ele.getAttribute("height");
        },
      },
    };
    returnable.set = {
      style: {
        height(ht: number) {
          ele.style.height = ht + "px";
        },
        width(wth: number) {
          ele.style.width = wth + "px";
        },
      },
      attr: {
        height(ht: number) {
          ele.setAttribute("height", ht.toString());
        },
        width(wth: number) {
          ele.setAttribute("width", wth.toString());
        },
      },
    };
    return returnable;
  };

  useEffect(() => {
    if (canvaRef.current) {
      const canvas = canvas_obj(canvaRef.current);
      let { ctx, dpi, set, get } = canvas;

      let frameCount = 0;
      let animationFrameId = 0;

      const dpi_adjust = () => {
        set.attr.height(get.style.height() * dpi);
        set.attr.width(get.style.width() * dpi);
        ctx.scale(dpi, dpi);
      };

      //Our draw came here
      const render = () => {
        frameCount++;
        dpi_adjust();
        draw(ctx, frameCount);
        animationFrameId = window.requestAnimationFrame(render);
      };
      render();

      return () => {
        window.cancelAnimationFrame(animationFrameId);
      };
    }
  }, [canvaRef.current, draw]);

  return (
    <canvas
      ref={canvaRef}
      {...props}
      style={{ ...props.style, touchAction: "none" }}
    />
  );
};
