import React, { useRef, useEffect, useCallback } from "react";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../../../app/store";
import { tick } from "../../../app/slices/game_slice";

export const Canvas = (
  props: JSX.IntrinsicAttributes &
    React.ClassAttributes<HTMLCanvasElement> &
    React.CanvasHTMLAttributes<HTMLCanvasElement>
) => {
  const dispatch = useDispatch();

  const width = useSelector((state: RootState) => state.game.width);
  const height = useSelector((state: RootState) => state.game.height);
  const world = useSelector((state: RootState) => state.game.world);
  const counter = useSelector((state: RootState) => state.game.counter);

  const CHARACTER_SIZE = 10;

  const canvaRef = useRef<HTMLCanvasElement>(null);

  const draw = useCallback(
    (ctx: CanvasRenderingContext2D, frameCount: number) => {
      ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

      ctx.textAlign = "center";
      ctx.textBaseline = "middle";
      ctx.font = "10px serif";

      world.forEach((item) =>
        ctx.fillText(
          item.char,
          item.x * CHARACTER_SIZE + CHARACTER_SIZE / 2,
          item.y * CHARACTER_SIZE + CHARACTER_SIZE / 2
        )
      );
    },
    [counter]
  );

  useEffect(() => {
    if (canvaRef.current) {
      let animationFrameId = 0;
      let frameCount = 0;

      const ctx = canvaRef.current.getContext("2d")!;
      //Our draw came here

      const render = () => {
        frameCount++;
        if (frameCount % 6 === 0) {
          dispatch(tick());
          draw(ctx, frameCount);
        }

        animationFrameId = window.requestAnimationFrame(render);
      };
      render();

      return () => {
        window.cancelAnimationFrame(animationFrameId);
      };
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [canvaRef.current, counter]);

  return (
    <canvas
      ref={canvaRef}
      {...props}
      width={width * CHARACTER_SIZE}
      height={height * CHARACTER_SIZE}
      style={{ ...props.style, touchAction: "none", border: "1px solid black" }}
    />
  );
};
